import { useRecoilState, useRecoilValue } from "recoil";
import { allCategories, categoryState, toDoSelector } from "../atoms";
import CreateToDo from "./CreateToDo";
import ToDo from "./ToDo";
import { useState } from "react";

function ToDoList(){
    const [NewAddCats, setNewAddCats] = useState<string[]>([]); //    
    const [isCreatingCat, setIsCreatingCat] = useState(false);  //    
    const [newCatInput, setNewCatInput] = useState(""); //   
    const allCats = [...Object.values(allCategories), ...NewAddCats]; //  + 


    const todoDoingDone = useRecoilValue(toDoSelector); //    ToDo 
    const [cat, setCat] = useRecoilState(categoryState); //   

    // select box   
    const onInput = (event:React.FormEvent<HTMLSelectElement>) => {
        //setCat(event.currentTarget.value as any);
        const value = event.currentTarget.value;
        setCat(value as any);

        if(value === "newCategory"){
            setIsCreatingCat(true);
        } else {
            setIsCreatingCat(false);
        }
    };

    // Add    
    const handleAddCat = () => {
        if(newCatInput.trim() === "") return; //  x

        setNewAddCats((prev) => [...prev, newCatInput]);
        setNewCatInput(""); // input 
        //setCat(""); // select ( ) 
        //setIsCreatingCat(false);
    }

    //  Delete 
    const handleDeleteCat = (catToDelete:string) => {
        setNewAddCats((prev) => prev.filter((cat) => cat !== catToDelete)); //    
    }


    return (
        <div>
            <h1>My To Do List</h1>
            <hr/>
            <select value={cat} onInput={onInput}>
                <option value="" disabled> .</option>
                <option value="newCategory">+ Add New Category</option>
                <option value={allCategories.TO_DO}>TO DO</option>
                <option value={allCategories.DOING}>DOING</option>
                <option value={allCategories.DONE}>DONE</option>

                {
                    NewAddCats.map((cat) => (
                        <option key={cat} value={cat}>{cat}</option>
                    ))
                }

            </select>


            {/*    */}
            {isCreatingCat && (
                <div>
                    <form
                        onSubmit={(e) => {
                            e.preventDefault(); //  
                            handleAddCat();     // Add 
                        }}
                    >
                        <input
                        type="text"
                        value={newCatInput}
                        onChange={(e) => setNewCatInput(e.target.value)}
                        placeholder="Make a new Category"
                        />
                        <button onClick={handleAddCat}>Add</button>
                    </form>


                    {/*   +    */}
                    <ul>
                        {
                            [
                                ...Object.values(allCategories),
                                ...NewAddCats
                            ].map((cat) => (
                                <li key={cat}>
                                    {cat}
                                    {NewAddCats.includes(cat) && (
                                        <button onClick={() => handleDeleteCat(cat)}></button>
                                    )}
                                </li>
                            ))
                        }
                    </ul>
                </div>
                )}



            <CreateToDo />
            {todoDoingDone?.map((atodoDoingDone) => 
            (
                <ToDo 
                    key={atodoDoingDone.id} 
                    {...atodoDoingDone}
                    allCategories={allCats} //    ToDo 
                />
            )
            
            )}

        </div>
    );
}

export default ToDoList;


